import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import AppMode from './App2';

export default class App {
  constructor(search) {
    this.apiKey = 'AIzaSyCVM1B1Kutwt3F-32Zz26EN2c6Y2Z0JmZU';
    this.search = search;
    this.state = {
      url: `https://www.googleapis.com/youtube/v3/search?key=${this.apiKey}&type=video&part=snippet&maxResults=15&q=${this.search}`,
    };
  }

  async start() {
    const model = new AppModel(this.state);
    const data = await model.getClipNames();
    const descript = await model.getClipDescriptions();
    const publish = await model.getClipPublishings();
    const channelTitle = await model.getClipChannelTitle();
    const id = await model.getClipID();
    const url2 = { url2: `https://www.googleapis.com/youtube/v3/videos?key=${this.apiKey}&id=${id}&part=snippet,statistics` };
    const model2 = new AppMode(url2);
    const views = await model2.getClipViews();
    const img = await model2.getClipImg();
    const idLink = await model2.getClipsId();
    const view = new AppView(data, descript, publish, channelTitle, views, img, idLink);
    view.render();
  }
}
