export default class AppMode {
  constructor(state) {
    this.state = state;
  }

  static extractClipViews(data) {
    return data.items.map(clip => clip.statistics.viewCount);
  }

  async getClipViews() {
    const { url2 } = this.state;
    const responce = await fetch(url2);
    const data = await responce.json();
    return AppMode.extractClipViews(data);
  }

  static extractClipImg(data) {
    return data.items.map(clip => clip.snippet.thumbnails.high.url);
  }

  async getClipImg() {
    const { url2 } = this.state;
    const responce = await fetch(url2);
    const data = await responce.json();
    return AppMode.extractClipImg(data);
  }

  static extractClipsId(data) {
    return data.items.map(clip => clip.id);
  }

  async getClipsId() {
    const { url2 } = this.state;
    const responce = await fetch(url2);
    const data = await responce.json();
    return AppMode.extractClipsId(data);
  }
}
