export default class AppView {
  constructor(titles, descriptions, publish, channelTitle, views, img, idLink, search) {
    this.titles = titles;
    this.descriptions = descriptions;
    this.published = publish;
    this.channelTitle = channelTitle;
    this.views = views;
    this.img = img;
    this.id = idLink;
    this.search = search;
  }

  render() {
    const div = document.createElement('div');
    div.classList.add('container');
    div.classList.add('clipBox');
    document.getElementById('containers').appendChild(div);

    const img = document.createElement('img');
    img.src = this.img.shift();
    document.querySelector('.container').appendChild(img);
    const titleLink = document.createElement('a');
    const link = this.id.shift();
    titleLink.setAttribute('href', `https://www.youtube.com/watch?v=${link}`);
    titleLink.innerHTML = this.titles.shift();
    document.querySelector('.container').appendChild(titleLink);

    const iconChannel = document.createElement('i');
    iconChannel.classList.add('fas');
    iconChannel.classList.add('fa-address-card');
    const channelTitle = document.createElement('div');
    channelTitle.classList.add('videoChannel');
    channelTitle.classList.add('channelTitle');
    channelTitle.innerHTML = this.channelTitle.shift();
    document.querySelector('.container').appendChild(channelTitle);
    document.querySelector('.videoChannel').appendChild(iconChannel);
    channelTitle.classList.remove('videoChannel');

    const iconPublish = document.createElement('i');
    iconPublish.classList.add('fas');
    iconPublish.classList.add('fa-calendar');
    const published = document.createElement('div');
    published.classList.add('videoPublish');
    published.innerHTML = this.published.shift().slice(0, 10);
    document.querySelector('.container').appendChild(published);
    document.querySelector('.videoPublish').appendChild(iconPublish);
    published.classList.remove('videoPublish');

    const iconViews = document.createElement('i');
    iconViews.classList.add('fas');
    iconViews.classList.add('fa-eye');
    const videoViews = document.createElement('div');
    videoViews.classList.add('videoViews');
    videoViews.innerHTML = this.views.shift();
    document.querySelector('.container').appendChild(videoViews);
    document.querySelector('.videoViews').appendChild(iconViews);
    videoViews.classList.remove('videoViews');

    const description = document.createElement('div');
    description.innerHTML = this.descriptions.shift();
    description.setAttribute('id', 'description');
    document.querySelector('.container').appendChild(description);
    global.console.log(document.body.childElementCount);
    div.classList.remove('container');
    this.render1();
    document.getElementById('nav').style.display = 'block';
  }

  render1() {
    if (document.getElementById('containers').childElementCount < 15) {
      this.render();
    }
  }
}
