import App from './controllers/App';
import '../style.css';

const meta = document.createElement('meta');
meta.setAttribute('name', 'viewport');
meta.setAttribute('content', 'width=device-width, initial-scale=1.0');
document.head.appendChild(meta);

const link = document.createElement('link');
link.setAttribute('rel', 'stylesheet');
link.setAttribute('href', 'https://use.fontawesome.com/releases/v5.8.2/css/all.css');
document.head.appendChild(link);

const header = document.createElement('div');
header.setAttribute('id', 'header');
document.body.appendChild(header);

const input = document.createElement('input');
input.setAttribute('id', 'input');
document.getElementById('header').appendChild(input);

const button = document.createElement('button');
button.classList.add('button');
button.innerHTML = 'Search';
document.getElementById('header').appendChild(button);

const main = document.createElement('div');
main.classList.add('main');
main.setAttribute('id', 'main');
document.body.appendChild(main);

const container = document.createElement('div');
container.classList.add('containers');
container.setAttribute('id', 'containers');
document.getElementById('main').appendChild(container);


button.addEventListener('click', () => {
  if (!document.getElementById('containers').firstChild) {
    const asd = new App(input.value);
    asd.start();
  } else {
    while (document.getElementById('containers').firstChild) {
      document.getElementById('containers').removeChild(document.getElementById('containers').firstChild);
    }
    const asd = new App(input.value);
    asd.start();
  }
});

const slider = document.getElementById('containers');
let isDown = false;
let startX;
// eslint-disable-next-line no-unused-vars
let scroll;
let translate = 0;

slider.addEventListener('mousedown', (e) => {
  document.getElementById('containers').classList.remove('containersVisible');
  document.getElementById('containers').classList.add('containers');
  isDown = true;
  slider.classList.add('active');
  startX = e.pageX - slider.offsetLeft + translate;
  scroll = slider.scrollLeft;
  global.console.log(scroll);
});

slider.addEventListener('touchstart', (e) => {
  isDown = true;
  slider.classList.add('active');
  startX = e.changedTouches[0].pageX - slider.offsetLeft;
  scroll = slider.scrollLeft;
  global.console.log(slider.offsetLeft);
});

slider.addEventListener('mouseleave', () => {
  isDown = false;
  slider.classList.remove('active');
});

slider.addEventListener('mouseup', () => {
  isDown = false;
  slider.classList.remove('active');
});

slider.addEventListener('touchend', () => {
  isDown = false;
  slider.classList.remove('active');
});

slider.addEventListener('mousemove', (e) => {
  if (!isDown) return;
  e.preventDefault();
  const x = e.pageX - slider.offsetLeft;
  const walk = x - startX;
  slider.scrollLeft = scroll - walk;
});

slider.addEventListener('touchmove', (e) => {
  if (!isDown) return;
  e.preventDefault();
  const x = e.changedTouches[0].pageX - slider.offsetLeft;
  const walk = x - startX;
  slider.scrollLeft = scroll - walk;
  global.console.log(slider.scrollLeft);
});

const nav = document.createElement('div');
nav.setAttribute('id', 'nav');
document.body.appendChild(nav);
document.getElementById('nav').style.display = 'none';

const buttonNav = document.createElement('button');
buttonNav.classList.add('buttonNavPrev');
buttonNav.innerHTML = 'Prev';
document.getElementById('nav').appendChild(buttonNav);

const buttonNav2 = document.createElement('button');
buttonNav2.classList.add('buttonNavNext');
buttonNav2.innerHTML = 'Next';
document.getElementById('nav').appendChild(buttonNav2);

document.getElementById('nav').firstChild.addEventListener('mousedown', () => {
  if (!(translate === 0)) {
    translate += document.body.clientWidth;
    slider.style.transform = `translateX(${translate}px)`;
    document.getElementById('containers').classList.add('containerSlide');
    global.console.log(slider.scrollLeft);
  }
});

document.getElementById('nav').firstChild.addEventListener('mouseup', () => {
  document.getElementById('containers').classList.remove('containerSlide');
  document.getElementById('containers').classList.remove('containers');
  document.getElementById('containers').classList.add('containersVisible');
});

document.getElementById('nav').lastChild.addEventListener('mousedown', () => {
  translate -= document.body.clientWidth;
  slider.style.transform = `translateX(${translate}px)`;
  document.getElementById('containers').classList.add('containerSlide');
});

document.getElementById('nav').lastChild.addEventListener('mouseup', () => {
  document.getElementById('containers').classList.remove('containerSlide');
  document.getElementById('containers').classList.remove('containers');
  document.getElementById('containers').classList.add('containersVisible');
});
